import uniIcons from "../../components/uni-ui/uni-icons/uni-icons.vue"
export default {
	components: {
		uniIcons
	},
	data() {
		return {
			historyData: [],
			hotKeywordList: null,
			results: [],
			value: '',
			openId: '',
			show: true
		};
	},
	methods: {
		getSearchRelated() {
			this.$api.getSearchRelated(this.openId).then(res => {
				if (res.status === 200) {
					this.historyData = res.data.historyData
					this.hotKeywordList = res.data.hotKeywordList
				}
			})
		},
		clear() {
			this.value = ''
			this.results = []
		},
		confirm() {
			this.$api.getSearch((this.value).trim()).then(res => {
				if (res.status === 200) {
					this.results = res.data.keywords
				}
				this.getAddSearch((this.value).trim())
			})
		},
		goSearch(keyword) {
			this.value = (keyword).trim()
			this.$api.getSearch(this.value).then(res => {
				if (res.status === 200) {
					this.results = res.data.keywords
				}
				this.getAddSearch((this.value).trim())
			})
		},
		getAddSearch(value) {
			this.$api.getAddSearch({
				keyword: value,
				openId: this.openId
			})
		},
		clearHistory() {
			uni.showModal({
				title: '清除记录',
				content: '是否清除历史浏览记录',
				success: (res) => {
					if (res.confirm) {
						this.$api.getEmptySearch({
							openId: this.openId
						}).then(res => {
							if (res.status === 200) {
								this.historyData = []
								uni.showToast({
									title: res.data.data,
									duration: 800
								});
							}
						})
					} else if (res.cancel) {
						uni.showToast({
							icon: "none",
							title: "您已取消清除记录",
							duration: 800
						});
					}
				}
			});
		},
		cancel() {
			this.value = ''
			this.results = []
		},
		gotoDetail(id) {
			uni.navigateTo({
				url: `/pages/goodsDetail/goodsDetail?id=${id}`
			})
		}
	},
	watch: {
		value(val) {
			// console.log(val);
			if (val === '') {
				this.getSearchRelated()
				this.results = []
			} else {
				this.goSearch(val)
			}
		}
	},
	onLoad() {
		this.openId = uni.getStorageSync('openId')
		this.getSearchRelated()
	}
}
